import axios from "axios";

import React, { useState, useEffect } from "react";
import {
  startOfWeek,
  endOfWeek,
  eachDayOfInterval,
  format,
  addWeeks,
  subWeeks,
} from "date-fns";
import { fr } from "date-fns/locale";
import "../../../styles/Timetable.css";

function Timetable() {
  const [currentWeek, setCurrentWeek] = useState(new Date());
  const [displayText, setDisplayText] = useState({});
  const [startTime, setStartTime] = useState(9);
  const [endTime, setEndTime] = useState(15);
  const [interval, setInterval] = useState(60);
  const [selectedSlot, setSelectedSlot] = useState(null);
  const [selectedDate, setSelectedDate] = useState(null);
  const [formData, setFormData] = useState({
    n_dossier: "",
  });
  const [isFormVisible, setIsFormVisible] = useState(false);
  const [appointments, setAppointments] = useState([]);

  const weekStart = startOfWeek(currentWeek, { locale: fr });
  const weekEnd = endOfWeek(currentWeek, { locale: fr });
  const weekDays = eachDayOfInterval({ start: weekStart, end: weekEnd });

  useEffect(() => {
    axios
      .get("http://localhost:5000/appointments")
      .then((response) => {
        console.log("Appointments fetched:", response.data); // Log the fetched data
        setAppointments(response.data); // Set the appointments state
      })
      .catch((error) => {
        console.error("Error fetching appointments:", error);
      });
  }, []);

  const handleSlotClick = (date, time) => {
    const correctedDate = new Date(date);
    correctedDate.setDate(correctedDate.getDate() + 1);
    setSelectedDate(correctedDate);
    setSelectedSlot(time);
    setIsFormVisible(true);

    const slotData = appointments.find(
      (app) => app.Day === format(date, "yyyy-MM-dd") && app.Hour === time
    );

    setFormData({ n_dossier: slotData ? slotData.PatientID : "" });
  };

  const handleSave = async () => {
    // Ensure that the form data is valid
    if (!formData.n_dossier) {
      alert("Veuillez remplir le champ N° Dossier.");
      return;
    }
  
    const existingAppointment = appointments.find(
      (app) =>
        app.Day === format(selectedDate, "yyyy-MM-dd") &&
        app.Hour === selectedSlot
    );
  
    const appointmentData = {
      patientID: formData.n_dossier,
      day: format(selectedDate, "yyyy-MM-dd"),
      hour: selectedSlot,
    };
  
    try {
      let result;
      if (existingAppointment) {
        // Update existing appointment
        result = await axios.put(
          `http://localhost:5000/appointments/${existingAppointment.RDV_ID}`,
          appointmentData
        );
      } else {
        // Create new appointment
        result = await axios.post(
          "http://localhost:5000/appointments",
          appointmentData
        );
      }
  
      // Update appointments state to trigger a re-render
      setAppointments((prevAppointments) => {
        if (existingAppointment) {
          return prevAppointments.map((appointment) =>
            appointment.RDV_ID === existingAppointment.RDV_ID
              ? { ...result.data.appointment }
              : appointment
          );
        } else {
          return [...prevAppointments, result.data.appointment];
        }
      });
  
      refetchAppointments();
  
      setIsFormVisible(false);
      setFormData({ n_dossier: "" });
    } catch (error) {
      console.error("Error saving/updating appointment:", error);
      alert(
        "Une erreur s'est produite lors de l'enregistrement du rendez-vous."
      );
    }
  };
  
  const handleUpdate = async (appointmentId) => {
    const updatedAppointmentData = {
      patientID: formData.n_dossier,
      day: format(selectedDate, "yyyy-MM-dd"),
      hour: selectedSlot,
    };

    try {
      const result = await axios.put(
        `http://localhost:5000/appointments/${appointmentId}`,
        updatedAppointmentData
      );
      setAppointments((prevAppointments) =>
        prevAppointments.map((appointment) =>
          appointment.RDV_ID === appointmentId
            ? result.data.appointment
            : appointment
        )
      );
      setIsFormVisible(false);
    } catch (error) {
      console.error("Error updating appointment:", error);
    }
  };

  const handleDelete = async (appointmentId) => {
    try {
      await axios.delete(`http://localhost:5000/appointments/${appointmentId}`);
      setAppointments((prevAppointments) =>
        prevAppointments.filter(
          (appointment) => appointment.RDV_ID !== appointmentId
        )
      );
      setIsFormVisible(false);
    } catch (error) {
      console.error("Error deleting appointment:", error);
    }
  };

  const refetchAppointments = async () => {
    try {
      const { data } = await axios.get("http://localhost:5000/appointments");
      setAppointments(data); // Update appointments state with new data
    } catch (error) {
      console.error("Error fetching appointments:", error);
    }
  };

  const formatTime = (hour, minute) => {
    const formattedHour = hour < 10 ? `0${hour}` : hour;
    const formattedMinute = minute < 10 ? `0${minute}` : minute;
    return `${formattedHour}:${formattedMinute}`;
  };

  const generateTimeSlots = () => {
    const slots = [];
    let currentHour = startTime;
    let currentMinute = 0;

    while (currentHour + currentMinute / 60 < endTime) {
      const startTimeFormatted = formatTime(currentHour, currentMinute);

      const totalMinutes = currentMinute + interval;
      currentHour += Math.floor(totalMinutes / 60);
      currentMinute = totalMinutes % 60;

      const endTimeFormatted = formatTime(currentHour, currentMinute);
      slots.push(`${startTimeFormatted} - ${endTimeFormatted}`);
    }

    return slots;
  };

  const timeSlots = generateTimeSlots();

  return (
    <div className="timetable-container">
      {isFormVisible && (
        <div className="popup-form">
          <div className="popup-form-content">
            <button
              className="popup-form-close"
              onClick={() => setIsFormVisible(false)}
            >
              ×
            </button>
            <h2>
              {appointments.find(
                (app) =>
                  app.date === format(selectedDate, "yyyy-MM-dd") &&
                  app.time === selectedSlot
              )
                ? "Modifier la Réservation"
                : "Réserver le Temps"}
              Réserver le Temps
            </h2>
            <form onSubmit={(e) => e.preventDefault()}>
              <div className="form-row">
                <label>
                  N° Dossier:
                  <input
                    type="text"
                    value={formData.n_dossier}
                    onChange={(e) =>
                      setFormData({ ...formData, n_dossier: e.target.value })
                    }
                  />
                </label>
              </div>
              <div className="form-actions">
                <button type="button" onClick={handleSave}>
                  Enregistrer
                </button>
                {appointments.find(
                  (app) =>
                    app.date === format(selectedDate, "yyyy-MM-dd") &&
                    app.time === selectedSlot
                ) && (
                  <button
                    type="button"
                    onClick={handleDelete}
                    className="delete-btn"
                  >
                    Supprimer
                  </button>
                )}
                <button
                  type="button"
                  onClick={() => setIsFormVisible(false)}
                  className="cancel-btn"
                >
                  Annuler
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      <div className="week-navigation">
        <button onClick={() => setCurrentWeek(subWeeks(currentWeek, 1))}>
          Previous Week
        </button>
        <span>{format(currentWeek, "MMMM yyyy", { locale: fr })}</span>
        <button onClick={() => setCurrentWeek(addWeeks(currentWeek, 1))}>
          Next Week
        </button>
      </div>

      <div className="controls">
        <label>
          Heure de début:
          <input
            type="number"
            value={startTime}
            onChange={(e) => {
              const value = Number(e.target.value);
              if (value >= 1 && value <= 24 && value < endTime)
                setStartTime(value);
            }}
            min="1"
            max="11"
          />
        </label>
        <label>
          Heure de fin:
          <input
            type="number"
            value={endTime}
            onChange={(e) => {
              const value = Number(e.target.value);
              if (value >= 1 && value <= 24 && value > startTime)
                setEndTime(value);
            }}
            min="12"
            max="22"
          />
        </label>
        <label>
          Intervalle:
          <select
            value={interval}
            onChange={(e) => setInterval(Number(e.target.value))}
          >
            <option value="10">10 minutes</option>
            <option value="15">15 minutes</option>
            <option value="20">20 minutes</option>
            <option value="25">25 minutes</option>
            <option value="30">30 minutes</option>
            <option value="40">40 minutes</option>
            <option value="45">45 minutes</option>
            <option value="60">1 heure</option>
          </select>
        </label>
      </div>

      <table>
        <thead>
          <tr>
            <th>Heure</th>
            {weekDays.map((day) => (
              <th key={day}>{format(day, "EEE dd/MM", { locale: fr })}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {timeSlots.map((time) => (
            <tr key={time}>
              <td>{time}</td>
              {weekDays.map((day) => {
                const dayAppointment = appointments.find(
                  (app) =>
                    app.Day === format(day, "yyyy-MM-dd") && app.Hour === time
                );

                return (
                  <td
                    key={day}
                    onClick={() => handleSlotClick(day, time)}
                    className={
                      dayAppointment ? "highlighted-cell" : "empty-slot"
                    }
                  >
                    {dayAppointment ? `${dayAppointment.PatientFullName}` : ""}
                  </td>
                );
              })}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Timetable;
